.review{
    width: 75%;
    margin: 0 auto;
    -webkit-box-shadow: 0 3rem 6rem rgba(0, 0, 0, .1);
            box-shadow: 0 3rem 6rem rgba(0, 0, 0, .1);
    background-color: #fff;
    border-radius: 3px;
    padding: 6rem;
    padding-left: 9rem;
    font-size: 1.6rem;
    -webkit-transform: skewX(-12deg);
        -ms-transform: skewX(-12deg);
            transform: skewX(-12deg);
}

.review__shape{
    width: 15rem;
    height: 15rem;
    float: left;
    -webkit-transform: translateX(-3rem) skewX(12deg);
        -ms-transform: translateX(-3rem) skewX(12deg);
            transform: translateX(-3rem) skewX(12deg);
    position: relative;
    overflow: hidden;
    border-radius: 50%;
}

.review__img{
    height: 100%;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
}

.review__text{
    -webkit-transform: skewX(12deg);
        -ms-transform: skewX(12deg);
            transform: skewX(12deg);
}

.review__caption{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, 20%);
        -ms-transform: translate(-50%, 20%);
            transform: translate(-50%, 20%); 
    color: #fff;
    text-transform: uppercase;
    font-size: 1.7rem;
    text-align: center;
    opacity: 0;
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

.review:hover .review__caption{
    opacity: 1;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); 
}

.review:hover .review__img{
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
    -webkit-filter: blur(3px) brightness(80%);
            filter: blur(3px) brightness(80%);
}

@supports((-webkit-clip-path: circle(50% at 50% 50%)) or (clip-path: circle(50% at 50% 50%))) or (-webkit-clip-path: circle(50% at 50% 50%)) {
    .review__shape{
        -webkit-clip-path: circle(50% at 50% 50%);
        clip-path: circle(50% at 50% 50%);
        shape-outside: circle(50% at 50% 50%);
        border-radius: none;
    }
}

/* Tablet portrait media */
@media (max-width: 56.25em){    /* width < 900? */
    .review{
        width: 100%; 
        padding: 4rem;
        padding-left: 6rem;
    }
}

/* Phone media */ 
@media (max-width: 37.5em){     /* width < 600? */ 
    .review{
        -webkit-transform: skewX(0);
            -ms-transform: skewX(0);
                transform: skewX(0);
    }
    
    .review__shape{ 
        width: 10rem;
        height: 10rem;
        -webkit-transform: translateX(-3rem) skewX(0);
            -ms-transform: translateX(-3rem) skewX(0);
                transform: translateX(-3rem) skewX(0);
    }
    .review__text{
        -webkit-transform: skewX(0);
            -ms-transform: skewX(0);
                transform: skewX(0);
    }
}


