.menu-box{
    background-color: rgba(255, 255, 255, 0.8);
    font-size: 1.5rem;
    padding: 2.5rem;
    text-align: center;
    border-radius: 3px;
    -webkit-box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, .15);
            box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, .15);
    -webkit-transition: -webkit-transform .3s;
    transition: -webkit-transform .3s;
    -o-transition: transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
    opacity: 0;
}

.menu-box-link{
    color: #777;
    text-decoration: none;
    -webkit-transition: -webkit-transform .3s;
    transition: -webkit-transform .3s;
    -o-transition: transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
}

@media (-moz-touch-enabled: 0), (pointer:fine) {
    .menu-box-link:hover{
        -webkit-transform: translateY(-1.5rem) scale(1.03);
            -ms-transform: translateY(-1.5rem) scale(1.03);
                transform: translateY(-1.5rem) scale(1.03);
        cursor: pointer;
    }
}


.menu-box__icon{
    font-size: 6rem;
    margin-bottom: .5rem;
    display: inline-block;
    background-image: -webkit-gradient(linear, left top, right top, from(#DD2C00), to(#BE2600));
    background-image: -webkit-linear-gradient(left, #DD2C00, #BE2600);
    background-image: -o-linear-gradient(left, #DD2C00, #BE2600);
    background-image: linear-gradient(to right, #DD2C00, #BE2600);
    -webkit-background-clip: text;
    color: transparent;
}



