.footer{
    background-color: #333;
    padding: 10rem 0;
    font-size: 1.4rem;
    color: #f7f7f7;
}

.footer__navigation{
    border-top: 1px solid #eee;
    padding-top: 2rem;
    display: inline-block;
}

.footer__list{
    list-style: none;
}

.footer__item{
    display: inline-block;
}
.footer__item:not(:last-child){
    margin-right: 1.5rem;
}

.footer__link{

}

.footer__link:link,
.footer__link:visited{
    color: #f7f7f7;
    background-color: #333;
    text-decoration: none;
    text-transform: uppercase;
    display: inline-block;
    -webkit-transition: all .2s;
    -o-transition: all .2s;
    transition: all .2s;
}

@media (-moz-touch-enabled: 0), (pointer:fine) {
    .footer__link:hover{
        color: #DD2C00;
        -webkit-box-shadow: 0 1rem 2rem rgba(0, 0, 0, .4);
                box-shadow: 0 1rem 2rem rgba(0, 0, 0, .4);
        -webkit-transform: rotate(5deg) scale(1.3);
            -ms-transform: rotate(5deg) scale(1.3);
                transform: rotate(5deg) scale(1.3);
    }
}

.footer__link:active{
    color: #DD2C00;
    -webkit-box-shadow: 0 1rem 2rem rgba(0, 0, 0, .4);
            box-shadow: 0 1rem 2rem rgba(0, 0, 0, .4);
    -webkit-transform: rotate(5deg) scale(1.3);
        -ms-transform: rotate(5deg) scale(1.3);
            transform: rotate(5deg) scale(1.3);
}

.footer__copyright{
    border-top: 1px solid #eee;
    padding-top: 2rem;
    width: 80%;
    float: right;
}

/* Tablet portrait media */
@media (max-width: 56.25em){    /* width < 900? */
    .footer{
        padding: 8rem 0;
    }

    .footer__navigation{
        width: 100%;
        text-align: center;
    }

    .footer__copyright{
        width: 100%;
        float: none;
    }
}




