.paragraph{
    font-size: 1.7rem;
    padding-bottom: 5rem;
}

.paragraph:not(:last-child){
    margin-bottom: 3rem;
}

/*Composition*/
.composition{
    position: relative;
}

.composition__photo{
    width: 55%;
    -webkit-box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, .4);
            box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, .4);
    border-radius: 2px;
    position: absolute;
    z-index: 10;
    -webkit-transition: all .2s;
    -o-transition: all .2s;
    transition: all .2s;
    outline-offset: 1.5rem;
}

@media (-moz-touch-enabled: 0), (pointer:fine) {
    .composition__photo:hover{
        outline: 1.5rem solid #BE2600;
        -webkit-transform: scale(1.05) translateY(-.5rem);
            -ms-transform: scale(1.05) translateY(-.5rem);
                transform: scale(1.05) translateY(-.5rem);
        -webkit-box-shadow: 0 2.5rem 4rem rgba(0, 0, 0, .5);
                box-shadow: 0 2.5rem 4rem rgba(0, 0, 0, .5);
        z-index: 11;
    }
}

.composition__photo--p1{
    left: 0;
    top: -2rem;
}

.composition__photo--p2{
    right: 0;
    top: 2rem;
}

.composition__photo--p3{
    left: 20%;
    top: 10rem;
}

@media (-moz-touch-enabled: 0), (pointer:fine) {
    .composition:hover .composition__photo:not(:hover){
        -webkit-transform: scale(0.95);
            -ms-transform: scale(0.95);
                transform: scale(0.95);
    }
}

/* Tablet portrait media */
@media (max-width: 56.25em){    /* width < 900? */
    .composition__photo{
        float: left;
        position: relative;
        width: 33.33333%;
        -webkit-box-shadow: 0 1.5rem 3rem rgba(0, 0, 0, .2);
                box-shadow: 0 1.5rem 3rem rgba(0, 0, 0, .2);
    }

    .composition__photo--p1{
        top: 1rem;
        -webkit-transform: scale(1.1);
            -ms-transform: scale(1.1);
                transform: scale(1.1);
    }
    
    .composition__photo--p2{
        top: -1rem;
        -webkit-transform: scale(1.3);
            -ms-transform: scale(1.3);
                transform: scale(1.3);
        z-index: 100;
    }
    
    .composition__photo--p3{
        left: 0;
        top: 1rem;
        -webkit-transform: scale(1.1);
            -ms-transform: scale(1.1);
                transform: scale(1.1);
    }
}






