.header{/*
    height: 85vh;*/
    height: 90vh;
    background-image: -webkit-gradient(
        linear,
        left top, right bottom,
        from(rgba(250, 0, 0, 0.36)), 
        to(rgba(250, 0, 0, 0.36))), 
        url("./headerbackground@0,25x.jpg");
    background-image: -webkit-linear-gradient(
        left top,
        rgba(250, 0, 0, 0.36), 
        rgba(250, 0, 0, 0.36)), 
        url("./headerbackground@0,25x.jpg");
    background-image: -o-linear-gradient(
        left top,
        rgba(250, 0, 0, 0.36), 
        rgba(250, 0, 0, 0.36)), 
        url("./headerbackground@0,25x.jpg");
    background-image: linear-gradient(
        to right bottom,
        rgba(250, 0, 0, 0.36), 
        rgba(250, 0, 0, 0.36)), 
        url("./headerbackground@0,25x.jpg");
    background-size: cover;
    background-position: top;
    position: relative;
    /*These 2 keep the image fixed in place*/
    background-repeat: no-repeat;
    background-attachment: fixed;
}

/* Resolution */
/* If screen resolution is good(retina display) and screen size bigger than 600px
    or screen resolution is low, but screen size is very big */
@media(-webkit-min-device-pixel-ratio: 2) and (min-width: 37.5em),
      (-o-min-device-pixel-ratio: 2/1) and (min-width: 37.5em),
      (min-resolution: 192dpi) and (min-width: 37.5em),
      (min-width: 112.5em){
        .header{
            background-image: -webkit-gradient(
                linear,
                left top, right bottom,
                from(rgba(250, 0, 0, 0.36)), 
                to(rgba(250, 0, 0, 0.36))), 
                url("./headerbackground@0,33x.jpg");
            background-image: -webkit-linear-gradient(
                left top,
                rgba(250, 0, 0, 0.36), 
                rgba(250, 0, 0, 0.36)), 
                url("./headerbackground@0,33x.jpg");
            background-image: -o-linear-gradient(
                left top,
                rgba(250, 0, 0, 0.36), 
                rgba(250, 0, 0, 0.36)), 
                url("./headerbackground@0,33x.jpg");
            background-image: linear-gradient(
                to right bottom,
                rgba(250, 0, 0, 0.36), 
                rgba(250, 0, 0, 0.36)), 
                url("./headerbackground@0,33x.jpg");
        }
}

@media (-moz-touch-enabled: 1), (pointer:coarse) {
    .header{
        background-attachment: scroll;
    }
}



