.heading-secondary {
    font-size: 3.5rem;
    text-transform: uppercase;
    font-weight: 400;
    display: inline-block;
    color: #DD2C00;
    letter-spacing: .2rem;
    -webkit-transition: all .2s;
    -o-transition: all .2s;
    transition: all .2s;
}

.heading-secondary:hover {
    -webkit-transform: skewY(2deg) skewX(15deg) scale(1.1);
        -ms-transform: skewY(2deg) skewX(15deg) scale(1.1);
            transform: skewY(2deg) skewX(15deg) scale(1.1);
    text-shadow: .5rem 1rem 2rem rgba(0, 0, 0, .2);
}

@supports (background-clip: text) or (-webkit-background-clip: text) {
    .heading-secondary {
        background-image: -webkit-gradient(linear, left top, right top, from(#DD2C00), to(#BE2600));
        background-image: -webkit-linear-gradient(left, #DD2C00, #BE2600);
        background-image: -o-linear-gradient(left, #DD2C00, #BE2600);
        background-image: linear-gradient(to right, #DD2C00, #BE2600);
        background-clip: text;
        -webkit-background-clip: text;
        color: transparent;
    }
}

/* Phone media */
@media (max-width: 37.5em){     /* width <= 600? */
    .heading-secondary{
        font-size: 2.5rem;
    }
}


