@font-face { 
    font-family: American Typewriter; 
    src: url('../../fonts/AmericanTypewriterRegular.ttf'); 
} 
@font-face { 
    font-family: American TypewriterBold; 
    src: url('../../fonts/AmericanTypewriterBold.ttf'); 
} 

*{
    margin: 0;
    padding: 0;
    -webkit-box-sizing: inherit;
            box-sizing: inherit;
}

html{
    /*This defines what 1rem is */
    font-size: 62.5%;   /* 1rem = 10px; 10px/16px = 62.5% */
}

body{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-family: "American Typewriter", "Helvetica Neue", Helvetica, Arial, sans-serif;
    /*font-weight: 100;*/
    /*font-size: 1.6rem;*/
    line-height: 1.7;
    color: #777;
    background-color: #f7f7f7;
    padding: 3rem;
}

::-moz-selection{
    background-color: #DD2C00;
    color: #f7f7f7
}

::selection{
    background-color: #DD2C00;
    color: #f7f7f7
}


/* --------- MEDIA --------- 
    Phone:    600px    =   37.5em
    Tab-port: 900px    =   56.25em
    Tab-land: 1200px   =   75em
    Big-Desk: 1800px   =   112.5em
*/

/* Tablet Landscape */
@media (max-width: 75em){   /* width < 1200? */
    html{
        font-size: 56.25%;  /* 1rem = 9px; 9px/16px = 62.5% */
    }
}

/* Tablet portrait media */
@media (max-width: 56.25em){    /* width < 900? */
    html{
        font-size: 50%;
    }
    
    body{
        padding: 0rem;
    }
}

/* Phone media */ /*
@media (max-width: 37.5em){  */   /* width < 600? */ /*
    html{
        font-size: 30%;
    }
    
    body{
        padding: 0rem;
    }
}*/

/* Big Desktop */
@media (min-width: 112.5em){
    html{
        font-size: 75%;
    }
}



